@charset "UTF-8";
/* Main SASS entry point */
/* Base styles */
/* Reset and Base Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  line-height: 1.6;
  color: #333;
  background-color: #f8f9fa; }

/* Skip Link for Accessibility */
.skip-link {
  position: absolute;
  top: -40px;
  left: 6px;
  background: black;
  color: white;
  padding: 8px;
  text-decoration: none;
  border-radius: 4px;
  z-index: 1000; }
  .skip-link:focus {
    top: 6px; }

/* Main Grid Container */
.grid-container {
  display: grid;
  grid-template-areas: "header" "navbar" "content" "footer";
  grid-template-rows: auto auto 1fr auto;
  min-height: 100vh;
  max-width: 1200px;
  margin: 0 auto;
  background: white;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1); }

/* Header Styles */
.header {
  grid-area: header;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 2rem 1rem;
  text-align: center; }

.header-content h1 {
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
  font-weight: 700; }

.header-content p {
  font-size: 1.1rem;
  opacity: 0.9; }

/* Navigation Styles */
.navbar {
  grid-area: navbar;
  background: #2c3e50;
  position: relative; }

.nav-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
  max-width: 1200px;
  margin: 0 auto; }

.nav-menu {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex; }
  .nav-menu li {
    position: relative; }
  .nav-menu a {
    display: block;
    color: white;
    text-decoration: none;
    padding: 1rem 1.5rem;
    transition: background-color 0.3s ease; }
    .nav-menu a:hover, .nav-menu a:focus {
      background-color: #34495e;
      outline: 2px solid #3498db;
      outline-offset: 2px;
      outline-offset: -2px; }

/* Dropdown Styles */
.has-dropdown {
  position: relative; }
  .has-dropdown > a::after {
    content: ' ▼';
    font-size: 0.8em;
    margin-left: 0.5rem;
    transition: transform 0.3s ease; }
  .has-dropdown.open > a::after {
    transform: rotate(180deg); }

.dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  background: #34495e;
  min-width: 200px;
  list-style: none;
  margin: 0;
  padding: 0;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  display: none;
  border-radius: 0 0 4px 4px; }
  .dropdown li {
    border-bottom: 1px solid #2c3e50; }
    .dropdown li:last-child {
      border-bottom: none; }
  .dropdown a {
    padding: 0.75rem 1rem;
    font-size: 0.9rem; }
    .dropdown a:hover, .dropdown a:focus {
      background-color: #3498db; }

.has-dropdown.open .dropdown {
  display: block; }

/* Mobile Menu Toggle */
.mobile-menu-toggle {
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  display: none;
  flex-direction: column;
  padding: 0.5rem; }
  .mobile-menu-toggle span {
    width: 25px;
    height: 3px;
    background: white;
    margin: 3px 0;
    transition: 0.3s ease;
    border-radius: 2px; }

/* Content Area Grid */
.content-area {
  grid-area: content;
  display: grid;
  grid-template-columns: 20% 80%;
  min-height: 600px; }

/* Sidebar Styles */
.sidebar {
  background: #ecf0f1;
  padding: 2rem 1rem;
  border-right: 1px solid #bdc3c7; }
  .sidebar h2 {
    color: #2c3e50;
    margin-bottom: 1rem;
    font-size: 1.3rem; }
  .sidebar h3 {
    color: #34495e;
    margin-bottom: 0.5rem;
    margin-top: 1.5rem;
    font-size: 1.1rem; }
  .sidebar ul {
    list-style: none;
    margin: 0;
    padding: 0;
    margin-bottom: 2rem; }
    .sidebar ul li {
      margin-bottom: 0.5rem; }
  .sidebar a {
    color: #3498db;
    text-decoration: none;
    transition: color 0.3s ease; }
    .sidebar a:hover, .sidebar a:focus {
      color: #2980b9;
      text-decoration: underline; }

.sidebar-widget {
  margin-bottom: 2rem;
  padding: 1rem;
  background: white;
  border-radius: 4px;
  border-left: 4px solid #3498db; }
  .sidebar-widget p {
    font-size: 0.9rem;
    color: #7f8c8d; }

/* Main Content Styles */
.main-content {
  padding: 2rem;
  background: white; }
  .main-content h1 {
    color: #2c3e50;
    margin-bottom: 1rem;
    font-size: 2rem; }
  .main-content h2 {
    color: #34495e;
    margin-top: 2rem;
    margin-bottom: 1rem;
    font-size: 1.5rem; }
  .main-content p {
    margin-bottom: 1rem;
    color: #555;
    line-height: 1.7; }
  .main-content article {
    margin-bottom: 2rem;
    padding-bottom: 2rem;
    border-bottom: 1px solid #ecf0f1; }
    .main-content article:last-child {
      border-bottom: none; }

/* Footer Styles */
.footer {
  grid-area: footer;
  background: #2c3e50;
  color: white;
  padding: 2rem 1rem;
  text-align: center; }

.footer-content p {
  margin-bottom: 0.5rem;
  opacity: 0.9; }
  .footer-content p:last-child {
    margin-bottom: 0;
    font-size: 0.9rem;
    opacity: 0.7; }

/* Responsive Design */
/* Tablet Styles */
@media (max-width: 768px) {
  .grid-container {
    margin: 0;
    box-shadow: none; }
  .header-content h1 {
    font-size: 2rem; }
  .content-area {
    grid-template-columns: 25% 75%; }
  .nav-menu {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background: #2c3e50;
    flex-direction: column;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); }
    .nav-menu.active {
      display: flex; }
  .mobile-menu-toggle {
    display: flex; }
  .dropdown {
    position: static;
    box-shadow: none;
    background: #34495e;
    border-radius: 0; }
  .has-dropdown > a::after {
    content: ' ▶'; }
  .has-dropdown.open > a::after {
    content: ' ▼'; } }

/* Mobile Styles */
@media (max-width: 480px) {
  .header {
    padding: 1.5rem 1rem; }
    .header h1 {
      font-size: 1.8rem; }
  .content-area {
    grid-template-columns: 1fr;
    grid-template-areas: "main" "sidebar"; }
  .sidebar {
    grid-area: sidebar;
    border-right: none;
    border-top: 1px solid #bdc3c7;
    padding: 1rem; }
  .main-content {
    grid-area: main;
    padding: 1rem; }
    .main-content h1 {
      font-size: 1.6rem; }
    .main-content h2 {
      font-size: 1.3rem; }
  .footer {
    padding: 1.5rem 1rem; } }

/* High Contrast and Accessibility */
@media (prefers-contrast: high) {
  .header {
    background: black; }
  .navbar {
    background: black; }
  .nav-menu a:hover, .nav-menu a:focus {
    background-color: white;
    color: black; } }

/* Reduced Motion */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important; } }

/* Focus Styles for Better Accessibility */
a:focus,
button:focus {
  outline: 2px solid #3498db;
  outline-offset: 2px; }

/* Print Styles */
@media print {
  .navbar,
  .sidebar,
  .footer {
    display: none; }
  .grid-container {
    box-shadow: none; }
  .content-area {
    grid-template-columns: 1fr; }
  .main-content {
    padding: 0; } }

